.week_about h1 {
    font-family: "Montserrat", sans-serif;
    color: white;
    font-size: 50px;

    text-align: center;
}

.week_about p {
    font-family: "Montserrat", sans-serif;
    color: white;
    font-size: 32px;

    text-align: center;
    word-wrap: break-word;
    
    margin-left: 25%;
    margin-right: 25%;
}

.pop-up {
    animation-name: fade-in;
    animation-duration: 1s;
    animation-fill-mode: forwards;
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}